import { isPhoneVailable, judgeNum } from './rule'

/**
 *
 *
 * @export Âà§Êñ≠ÊµèËßàÂô®Á±ªÂûã
 * @return ÊµèËßàÂô®Á±ªÂûã
 */
export function myBrowser() {
  const userAgent = navigator.userAgent // ÂèñÂæóÊµèËßàÂô®ÁöÑuserAgentÂ≠óÁ¨¶‰∏≤

  const isOpera = userAgent.indexOf('Opera') > -1

  if (isOpera) {
    return 'Opera'
  } // Âà§Êñ≠ÊòØÂê¶OperaÊµèËßàÂô®

  if (userAgent.indexOf('Firefox') > -1) {
    return 'FF'
  } // Âà§Êñ≠ÊòØÂê¶FirefoxÊµèËßàÂô®

  if (userAgent.indexOf('Chrome') > -1) {
    return 'Chrome'
  }

  if (userAgent.indexOf('Safari') > -1) {
    return 'Safari'
  } // Âà§Êñ≠ÊòØÂê¶SafariÊµèËßàÂô®

  if (userAgent.indexOf('compatible') > -1 && userAgent.indexOf('MSIE') > -1 && !isOpera) {
    return 'IE'
  } // Âà§Êñ≠ÊòØÂê¶IEÊµèËßàÂô®
}

/**
 *
 * @export ÊâãÊú∫Âè∑Âä†ÊòüÂè∑
 * @return {*}
 */
export function telEncry(value: string) {
  if (!value || !judgeNum(value.toString()) || !isPhoneVailable(value)) {
    return 0
  }
  value = value.replace(value.substring(3, 7), '****')
  return value
}

/**
 *
 *
 * @export getType Ëé∑ÂèñÂÖÉÁ¥†Á±ªÂûã
 * @param {*} value
 * @return {*}
 */
export function getType(value: any) {
  return Object.prototype.toString.call(value).slice(8, -1)
}
/**
 * @export browserVersion ÁßªÂä®Á´ØÊµèËßàÂô®„ÄÅÂÜÖÊ†∏Á≠âÂà§Êñ≠
 * @param trident IEÂÜÖÊ†∏
 * @param presto operaÂÜÖÊ†∏
 * @param webKit ËãπÊûú„ÄÅË∞∑Ê≠åÂÜÖÊ†∏
 * @param gecko ÊòØÂê¶‰∏∫ÁßªÂä®ÁªàÁ´Ø
 * @param ios iosÁªàÁ´Ø
 * @param android androidÁªàÁ´Ø
 * @param iPhone iPhoneÊàñËÄÖQQHDÊµèËßàÂô®
 * @param iPad ÊòØÂê¶iPad
 * @param webApp webÂ∫îÁî®Á®ãÂ∫è
 * @param weixin ÊòØÂê¶ÂæÆ‰ø°
 * @return {*}
 */
export function browserVersion() {
  const u = navigator.userAgent
  return {
    trident: u.indexOf('Trident') > -1, // IEÂÜÖÊ†∏
    presto: u.indexOf('Presto') > -1, // operaÂÜÖÊ†∏
    webKit: u.indexOf('AppleWebKit') > -1, // ËãπÊûú„ÄÅË∞∑Ê≠åÂÜÖÊ†∏
    gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') === -1, // ÁÅ´ÁãêÂÜÖÊ†∏
    mobile: !!u.match(/AppleWebKit.*Mobile.*/), // ÊòØÂê¶‰∏∫ÁßªÂä®ÁªàÁ´Ø
    ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), // iosÁªàÁ´Ø
    android: u.indexOf('Android') > -1 || u.indexOf('Adr') > -1, // androidÁªàÁ´Ø
    iPhone: u.indexOf('iPhone') > -1, // ÊòØÂê¶‰∏∫iPhoneÊàñËÄÖQQHDÊµèËßàÂô®
    iPad: u.indexOf('iPad') > -1, // ÊòØÂê¶iPad
    webApp: u.indexOf('Safari') === -1, // ÊòØÂê¶webÂ∫îÁî®Á®ãÂ∫èÔºåÊ≤°ÊúâÂ§¥ÈÉ®‰∏éÂ∫ïÈÉ®
    weixin: u.indexOf('MicroMessenger') > -1, // ÊòØÂê¶ÂæÆ‰ø°
  }
}

/**
 * @desc Ëé∑ÂèñÂΩìÂâçÈ°µÈù¢urlÁöÑÊüê‰∏™ÂèÇÊï∞(window.location.search)
 * @param {string} url
 * @param {string} param Ë¶ÅËé∑ÂèñÁöÑÂèÇÊï∞keyÂÄº
 * @return {string}
 */
export function getUrlArg(param: string): string {
  const url = window.location.href.replace('#/', '')
  const arr = url.split('?')
  const argStr = arr.length > 1 ? arr[1] : ''
  if (!argStr) {
    return ''
  }
  const arr2 = argStr.split('&').map(item => item.split('='))
  const key = param.toString()
  if (key) {
    const res = arr2.find(item => item[0] === key)
    if (res) {
      return res.length > 1 ? decodeURI(res[1]).toString() : ''
    } else {
      return ''
    }
  } else {
    return ''
  }
}

/**
 * @desc Ëé∑ÂèñurlÁöÑÊâÄÊúâÂèÇÊï∞ÂèÇÊï∞ÔºåËøîÂõû‰∏Ä‰∏™ÂØπË±°
 * @param {string} url
 * @return {object}
 */
export function getUrlQuery(url: string = window.location.href) {
  const obj: any = {}
  let temp
  const paramsGroup = url.replace('#/', '').split('?')[1] // name=QinYanFei&age=22&sex=0&phone=13083771680
  if (!paramsGroup) {
    return {}
  }
  const paramsObj = paramsGroup.split('&') // ["name=QinYanFei", "age=22", "sex=0", "phone=13083771680"]
  for (const index of paramsObj) {
    // for inÊòØÈÅçÂéÜÔºàobjectÔºâÈîÆÂêçÔºåfor ofÊòØÈÅçÂéÜÔºàarrayÔºâÈîÆÂÄº„ÄÇ
    temp = index.split('=') // ‰æùÊ¨°["name", "QinYanFei"]  ["age", "22"]  ["sex", "0"] ["phone", "13083771680"]
    obj[temp[0]] = decodeURI(temp[1]) // ÈîÆÂÄºÔºöÈîÆÂêç
  }
  return obj
}
