import { judgeNum } from './rule'
/**
 * @function interestRate Âà©ÁéáËΩ¨ÂåñÊòæÁ§∫ÊñπÊ≥ï
 * @param  value ‰º†ÂÖ•Êï∞Â≠ó
 * @param  word ‰º†ÂÖ•Ê≠£Ë¥üÂÄº
 */
export function interestRate(value: any, word: any) {
  if (!value || !judgeNum(value)) {
    return 0
  }
  value = value.replace(value, word + value + '%')
  return value
}

/**
 * @function interceptDot Á°ÆËÆ§ÈáëÈ¢ù‰øùÁïôÂ∞èÊï∞ÂêéÂá†‰ΩçÊñπÊ≥ï
 * @param  value ‰º†ÂÖ•Êï∞Â≠ó
 * @param  count ‰º†ÂÖ•‰øùÁïôÁöÑ‰ΩçÊï∞
 * @param  roundtag ÂΩìcount=0Êó∂ÔºåceilÔºöÂêë‰∏äÂèñÊï¥ÔºåfloorÔºöÊÉ≥‰∏ãÂèñÊï¥Ôºå‰∏ç‰º†ÂèÇÊï∞Â∞±ÈªòËÆ§Êà™ÂèñÊï¥Êï∞
 */
export function interceptDot(value: any, count: any, roundtag: string) {
  if (!value || !judgeNum(value) || !judgeNum(count)) {
    return 0
  }
  if (Number(count) === 0) {
    if (roundtag === 'ceil') {
      return Math.ceil(value)
    } else if (roundtag === 'floor') {
      return Math.floor(value)
    } else {
      return parseInt(value, 0)
    }
  }
  if (roundtag === 'round') {
    count = count ? parseInt(count, 0) : 0
    if (count <= 0) {
      return Math.round(value)
    }
    value = Math.round(value * Math.pow(10, count)) / Math.pow(10, count) // ÂõõËàç‰∫îÂÖ•
    value = Number(value).toFixed(count) // Ë°•Ë∂≥‰ΩçÊï∞
  } else {
    value = Number(value)
      .toFixed(Number(count) + 1)
      .slice(0, -1)
    // const reg = new RegExp(`(\\d+\\.?\\d{0,${count}})[\\.\\d]*`);
    // value= (value + '.' + '0'.repeat(count)).replace(reg, '$1' + '0'.repeat(count - 1)).replace(reg, '$1');
  }
  return value
}

/**
 * @desc Â∞ÜÊï∞Â≠óËΩ¨Âåñ‰∏∫ ‰∏á„ÄÅ‰∫øÂçï‰Ωç
 * @param number num
 * @return {string}
 */
export function unitConvert(num: number) {
  if (!num) {
    return ''
  }
  const moneyUnits = ['ÂÖÉ', '‰∏á', '‰∫ø', '‰∏á‰∫ø']
  const dividend = 10000
  let curentNum = num
  // ËΩ¨Êç¢Êï∞Â≠ó
  let curentUnit = moneyUnits[0]
  // ËΩ¨Êç¢Âçï‰Ωç
  for (let i = 0; i < 4; i++) {
    curentUnit = moneyUnits[i]
    if (strNumSize(curentNum) < 5) {
      break
    }
    curentNum = curentNum / dividend
  }
  const m = { num: '', unit: '' }
  m.num = curentNum.toFixed(2)
  m.unit = curentUnit
  return m.num + m.unit
}
// Ëé∑ÂèñÂÖ•ÂèÇÁöÑ‰ΩçÊï∞
function strNumSize(tempNum: number) {
  const stringNum = tempNum.toString()
  const index = stringNum.indexOf('.')
  let newNum = stringNum
  if (index !== -1) {
    newNum = stringNum.substring(0, index)
  }
  return newNum.length
}

/**
 * @desc Â∞èÊï∞ËΩ¨‰∏∫ÁôæÂàÜÊï∞ÔºåÂä†‰∏ä%
 * @param {number} point
 * @return string
 */
export function toPercent(point: number) {
  if (!point) {
    return ''
  }
  let str = Number(point * 100).toFixed(2)
  str += '%'
  return str
}
