/**
 * @desc Ëé∑ÂèñÊüêÂπ¥ÊüêÊúàÁöÑÊÄªÂ§©Êï∞
 * @param {number} year Âπ¥‰ªΩ
 * @param {number} month Êúà‰ªΩ
 * @return {*} ËøîÂõûÊüêÂπ¥ÊüêÊúàÁöÑÂ§©Êï∞
 */
const getMonthDaysCount = function (year: number, month: number) {
  return new Date(year, month, 0).getDate()
}

/**
 * @desc Ê†ºÂºèÂåñÊó∂Èó¥
 * @param {*} date Êó∂Èó¥
 * @param {string} fmt Ê†ºÂºèÔºåeg: ''yyyy-MM-dd HH:mm:ss'
 * @return {*} Ê†ºÂºèÂåñÂêéÁöÑÊó∂Èó¥Ê†ºÂºè
 */
const formatDate = function (date: any, fmt: string) {
  if (date == null || date === '' || date === undefined) return null
  try {
    if (typeof date === 'string' && date.indexOf('-') !== -1) {
      date = date.replace(/-/g, '/')
    }
    date = new Date(date)
  } catch (e) {
    // eslint-disable-next-line no-self-assign
    date = date
  }
  fmt = fmt ? fmt : 'yyyy-MM-dd'
  const o: any = {
    'M+': date.getMonth() + 1, // Êúà‰ªΩ
    'd+': date.getDate(), // Êó•
    'H+': date.getHours(), // Â∞èÊó∂
    'm+': date.getMinutes(), // ÂàÜ
    's+': date.getSeconds(), // Áßí
    'q+': Math.floor((date.getMonth() + 3) / 3), // Â≠£Â∫¶
    S: date.getMilliseconds(), // ÊØ´Áßí
  }
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
    for (const k in o) {
      if (new RegExp('(' + k + ')').test(fmt)) {
        fmt = fmt.replace(
          RegExp.$1,
          RegExp.$1.length === 1 ? o[k] : ('00' + o[k]).substring(('' + o[k]).length)
        )
      }
    }
  }
  return fmt
}

/**
 * @desc ËÆ°ÁÆó‰∏§‰∏™Êó•Êúü‰πãÈó¥Áõ∏Â∑ÆÂá†‰∏™Êúà
 * @param {string} date1 eg: 2021-02-30
 * @param {string} date2
 * @return {*} Áõ∏Â∑ÆÁöÑÊúà‰ªΩÊï∞
 */
const getMonths = (date1: string, date2: string) => {
  // Áî®-ÂàÜÊàêÊï∞ÁªÑ
  const d1 = date1.split('-')
  const d2 = date2.split('-')
  // Ëé∑ÂèñÂπ¥,ÊúàÊï∞
  const year1 = parseInt(d1[0], 0)
  const month1 = parseInt(d1[1], 0)
  const year2 = parseInt(d2[0], 0)
  const month2 = parseInt(d2[1], 0)
  // ÈÄöËøáÂπ¥,ÊúàÂ∑ÆËÆ°ÁÆóÊúà‰ªΩÂ∑Æ
  const months = (year2 - year1) * 12 + (month2 - month1)
  return months
}

/**
 * @desc Â∞ÜÊó∂Èó¥ËΩ¨ÂåñÊàê‚ÄúXXÂàÜÈíüÂâç‚ÄùÊàñ‚ÄúXXÂ∞èÊó∂Ââç‚ÄùÊàñ‚Äúyyyy-mm-dd  hh:mm:ss‚Äù
 * @param {number} timestamp Êó∂Èó¥Êà≥
 * @return {*}
 */
const dateToDesc = function (timestamp: number) {
  timestamp = Number(timestamp)
  let timeDiffer = new Date().getTime() - timestamp
  timeDiffer = timeDiffer / 1000 / 60
  if (timeDiffer < 60) {
    return Math.ceil(timeDiffer) + 'ÂàÜÈíüÂâç'
  }
  timeDiffer = timeDiffer / 60
  if (timeDiffer < 24) {
    return Math.ceil(timeDiffer) + 'Â∞èÊó∂Ââç'
  }
  const date = new Date(timestamp)
  const tempDate = new Date()
  const year = date.getFullYear()
  const tempYear = tempDate.getFullYear()
  let month: number | string = date.getMonth() + 1
  const tempMonth = tempDate.getMonth() + 1
  let day: number | string = date.getDate()
  const tempDay = tempDate.getDate()
  let hour: number | string = date.getHours()
  let minutes: number | string = date.getMinutes()

  let result = ' '
  if (year !== tempYear) {
    result += year + '-'
  }
  if (month !== tempMonth || day !== tempDay) {
    month = month < 10 ? '0' + month : month
    day = day < 10 ? '0' + day : day
    result += month + '-' + day + ' '
  }
  hour = hour < 10 ? '0' + hour : hour
  minutes = minutes < 10 ? '0' + minutes : minutes
  result += hour + ':' + minutes
  return result
}

/**
 * @desc Ëé∑Âèñ‰º†ÂÖ•ÁöÑÊó∂Èó¥ÊòØÊòüÊúüÂá†
 * @param {string} value eg: '2021-02-01'
 * @return {*} ËøîÂõûÊòüÊúüÂá† eg: ÊòüÊúü‰∫î
 */
const getWeekZh = function (value: string) {
  const dateArray: any = value.split('-')
  const zhWeek =
    'ÊòüÊúü' +
    'Êó•‰∏Ä‰∫å‰∏âÂõõ‰∫îÂÖ≠'.charAt(
      new Date(dateArray[0], parseInt(dateArray[1], 0) - 1, dateArray[2]).getDay()
    )
  return zhWeek
}

/**
 * @desc Ëé∑Âèñ‰ªäÂπ¥ËøòÂâ©Â§öÂ∞ëÂ§©
 */
const restDayOfYear = function () {
  const fullyear: number = new Date().getFullYear()
  const nextyear: number = fullyear + 1
  const lastday: any = new Date(new Date(nextyear, 0, 1).getTime() - 1) // Êú¨Âπ¥ÁöÑÊúÄÂêé‰∏ÄÊØ´ÁßíÔºö
  const now: any = new Date()
  const diff = lastday - now // ÊØ´ÁßíÊï∞

  return Math.ceil(diff / (1000 * 60 * 60 * 24))
}

export { getMonthDaysCount, formatDate, getMonths, dateToDesc, getWeekZh, restDayOfYear }
